<!---->
<template>
  <div
    v-if="value > count"
    class="show-more-data cursor-pointer"
    :class="moreStyle"
    @click="handle">
    {{showMoreData ? expendText : closeText}}
    <i v-if="showIcon" class="el fz-10" :class="showMoreData ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"/>
  </div>
</template>

<script>
export default {
  name: 'show-more',
  title: '',
  desc: '',
  data () {
    return {
    }
  },
  methods: {
    handle () {
      this.$emit('handle')
    }
  },
  /*
    * value: list
    * showMoreData: 
    * count: 5
    * */
  props: {
    value: {
      type: Number,
      default: 0
    },
    showMoreData: {
      type: Boolean,
      default: false
    },
    count: {
      type: Number,
      default: 6
    },
    expendText: {
      type: String,
      default: ''
    },
    closeText: {
      type: String,
      default: ''
    },
    //  : fontStyle
    moreStyle: {
      type: String,
      default: ''
    },
    showIcon: {
      type: Boolean,
      default: true
    }
  }
}
</script>
