/**
 * @file xhr 
 * @author yun.wu
 * @module utils/xhr
 */
/**
 * xhr
 * @private
 * @returns {XMLHttpRequest}
 */
function getXhr () {
  return new XMLHttpRequest()
}

/**
 * get
 * @param url geturl
 * @returns {Promise<any>} 
 */
function get (url) {
  const xhr = getXhr()
  return new Promise((resolve, reject) => {
    xhr.open('GET', url, false)
    xhr.onload = function () {
      if (xhr.status === 200) {
        resolve(xhr.response)
      }
    }
    xhr.send()
  })
}

export default {
  get: get
}
