let config
const defaultConfig = {
  themeTypeName: '',
  themeName: '',
  allotMoneyName: '',
  allotName: '',
  themeSortName: '',
  govTitle: '',
  themeTSortName: '',
  reportName: '',
  funcName: '',
  budgetName: '',
  setupName: ''
}
export function initData () {
  config = {
    ...defaultConfig,
    ...window.Z.service.robot.common.enumConfig.getValue('regionConfig.public.system.variable', {}, 'root')
  }
}

export function getVariable (id, more = '') {
  if (!config) initData()
  return (config[id] || '') + more
}
