/**
 * @file 
 * @module global/date
 * @requires moment
 */
/**
 * 
 * @constant moment
 */
import moment from 'moment'

/**
 * 
 * @type {Number|null}
 */
let timestamp

/**
 * moment  
 */
export function init () {
  moment.defineLocale('zh-cn', {
    months: '___________'.split(
      '_'
    ),
    monthsShort: '1_2_3_4_5_6_7_8_9_10_11_12'.split(
      '_'
    ),
    weekdays: '______'.split('_'),
    weekdaysShort: '______'.split('_'),
    weekdaysMin: '______'.split('_'),
    longDateFormat: {
      LT: 'HH:mm',
      LTS: 'HH:mm:ss',
      L: 'YYYY/MM/DD',
      LL: 'YYYYMD',
      LLL: 'YYYYMDAhmm',
      LLLL: 'YYYYMDddddAhmm',
      l: 'YYYY/M/D',
      ll: 'YYYYMD',
      lll: 'YYYYMD HH:mm',
      llll: 'YYYYMDdddd HH:mm'
    },
    meridiemParse: /|||||/,
    meridiemHour: function (hour, meridiem) {
      if (hour === 12) {
        hour = 0
      }
      if (meridiem === '' || meridiem === '' || meridiem === '') {
        return hour
      } else if (meridiem === '' || meridiem === '') {
        return hour + 12
      } else {
        // ''
        return hour >= 11 ? hour : hour + 12
      }
    },
    meridiem: function (hour, minute, isLower) {
      var hm = hour * 100 + minute
      if (hm < 600) {
        return ''
      } else if (hm < 900) {
        return ''
      } else if (hm < 1130) {
        return ''
      } else if (hm < 1230) {
        return ''
      } else if (hm < 1800) {
        return ''
      } else {
        return ''
      }
    },
    calendar: {
      sameDay: '[]LT',
      nextDay: '[]LT',
      nextWeek: function (now) {
        if (now.week() !== this.week()) {
          return '[]dddLT'
        } else {
          return '[]dddLT'
        }
      },
      lastDay: '[]LT',
      lastWeek: function (now) {
        if (this.week() !== now.week()) {
          return '[]dddLT'
        } else {
          return '[]dddLT'
        }
      },
      sameElse: 'L'
    },
    dayOfMonthOrdinalParse: /\d{1,2}(||)/,
    ordinal: function (number, period) {
      switch (period) {
        case 'd':
        case 'D':
        case 'DDD':
          return number + ''
        case 'M':
          return number + ''
        case 'w':
        case 'W':
          return number + ''
        default:
          return number
      }
    },
    relativeTime: {
      future: '%s',
      past: '%s',
      s: '',
      ss: '%d ',
      m: '1 ',
      mm: '%d ',
      h: '1 ',
      hh: '%d ',
      d: '1 ',
      dd: '%d ',
      w: '1 ',
      ww: '%d ',
      M: '1 ',
      MM: '%d ',
      y: '1 ',
      yy: '%d '
    },
    week: {
      // GB/T 7408-1994ISO 8601:1988
      dow: 1, // Monday is the first day of the week.
      doy: 4 // The week that contains Jan 4th is the first week of the year.
    }
  })
}
/**
 * 
 * @param {any} value 
 * @param {String} format 
 * @returns {String} 
 */
export function timestampFormat (value, format) {
  if (!value) return ''
  return moment(value).format(format)
}

/**
 * 
 * @param {any} value 
 * @param {String} format=YYYY-MM-DD HH:mm:ss 
 * @returns {string} 
 */
export function format (value, format = 'YYYY-MM-DD HH:mm:ss') {
  if (!value) value = new Date()
  return moment(value).format(format)
}

/**
 * @desc  
 * @param {timeStamp} start 
 * @param {timeStamp} end   
 * @param {Boolean}  suffix=false  : XX
 * @return {String} desc    
 */
export function getRelativeTimeDesc (start, end, suffix = false) {
  const _start = moment(start)
  const _end = moment(end)
  return _start.from(_end, suffix)
}

/**
 * 
 * @return {Number} 
 */
export function getTimestamp () {
  return timestamp
}

/**
 * 
 * @param time {Number} 
 */
export function setTimestamp (time) {
  //  
  if (timestamp && timestamp > time) { return }
  timestamp = time
}

/**
 * 
 * @param {any} date 
 * @returns {String} 
 */
export function showTimestamp (date) {
  let defaultFormat = 'YYYY-MM-DD'
  if (date.length > 10) defaultFormat = 'YYYY-MM-DD HH:mm: ss'
  return moment(date, defaultFormat)
}

function changeTimeStamp (timeTemp) {
  const time = (new Date(timeTemp).getTime()) / 1000
  return time
}
// 
export function formatDate (timeTemp) {
  if (Array.isArray(timeTemp)) {
    if (!timeTemp.length) return ['', '']
    return timeTemp.map(date => changeTimeStamp(date))
  } else {
    return timeTemp ? changeTimeStamp(timeTemp) : ''
  }
}
