<template>
  <div id="opinion" v-if="showOpinion.visible">
    <el-dialog :title="title"
               :before-close="handleClose"
               :visible.sync="showOpinion.visible"
               class="template-dialog-container height-body-full box-white">
      <div class="dislogContent">
        <div class="opinion-title">{{isDetail ? '' : ''}}</div>
        <el-input class="unify" type="textarea" :disabled="isDetail" v-model="showOpinion.opinionText"  placeholder="" maxlength="50" show-word-limit></el-input>
        <rd-upload :files.sync="attachments" :onlineFile="true" :read="isDetail">
          <el-button icon="el-icon-plus"></el-button>
        </rd-upload>
      </div>
      <rd-btns :data="btns"></rd-btns>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "opinion",
  title: "",
  props: {
    title: {
      type: String,
      default: ''
    },
    showOpinion: {
      type: Object,
      default: {}
    },
    showDate: {
      type: Boolean,
      default: false
    },
    isDetail: {
      type: Boolean,
      default: false
    },
    attachmentFiles: {
      type: Array,
      default: () => []
    },
    fileList: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      btns: [
        {
          label: '',
          callback: this.cancel
        },
        {
          label: '',
          callback: this.confirm,
          type: 'primary'
        }
      ],
      attachments: this.fileList
    }
  },
  computed: {
    getFileList () {
      return this.fileList || []
    }
  },
  methods: {
    // 
    confirm () {
      this.$emit('confirm', this.attachments)
    },
    // 
    cancel () {
      this.handleClose()
    },
    // 
    handleClose () {
      this.$emit('closeDialog')
    }
  }
}
</script>
