<template>
  <div id="check" v-if="visible">
    <el-dialog title=""
               :before-close="handleClose"
               :visible.sync="visible"
               class="template-dialog-container height-body-full box-white">
      <div class="dislogContent" :style="{paddingBottom: noNormal ? '50px': ''}">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="" v-if="!noNormal">
            <el-select class="unify" v-model="form.agreeOrReject" placeholder="" @change="chooseOpinion">
              <el-option label="" value="Y"></el-option>
              <el-option label="" value="N"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input class="unify" type="textarea" v-model="form.operateOpinion"  placeholder="" maxlength="50" show-word-limit></el-input>
          </el-form-item>
        </el-form>
      </div>
      <rd-btns :data="btns"></rd-btns>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "Check",
  title: "",
  props: {
    noNormal: {
      type: Boolean,
      default: false
    },
    visible: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      form: {
        agreeOrReject: '',
        operateOpinion: ''
      },
      btns: [
        {
          label: '',
          callback: this.reject
        },
        {
          label: '',
          callback: this.agree,
          type: 'primary'
        }
      ]
    }
  },
  methods: {
    // 
    chooseOpinion (val) {
      this.form.operateOpinion = val === 'agree' ? '' : val === 'reject' ? '' : ''
    },
    // 
    agree () {
      const { operateOpinion, agreeOrReject } = this.form
      if (!operateOpinion || (!agreeOrReject && !this.noNormal)) return this.$message.error('')
      const param = JSON.parse(JSON.stringify(this.form))
      this.form = {
        agreeOrReject: '',
        operateOpinion: ''
      }
      this.$emit('agree', param)
    },
    // 
    reject () {
      this.handleClose()
    },
    // 
    handleClose () {
      this.form = {
        agreeOrReject: '',
        operateOpinion: ''
      }
      this.$emit('closeDialog')
    }
  }
}
</script>
